import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter, MemoryRouter } from "react-router-dom";
import { CssBaseline } from "@mui/material";
import { StyledEngineProvider } from "@mui/styled-engine";

// This function mounts the React app.
let root = null; // Global React root

function start() {
  if (typeof requirejs === "undefined") {
    console.error("❌ requirejs is not defined. PlatformAPI won't load.");
    return;
  }

  requirejs(["DS/PlatformAPI/PlatformAPI"], (PlatformAPI) => {
    window.PlatformAPI = PlatformAPI;

    // ⏬ Dynamically import App after PlatformAPI is loaded
    import("./app/App").then(({ default: App }) => {
      // Find the root element within the widget's body (if available)
      let rootElement =
        window.widget?.body?.querySelector("#root") ||
        document.getElementById("root");

      // If no root element exists, create one and append it
      if (!rootElement) {
        rootElement = document.createElement("div");
        rootElement.id = "root";
        if (window.widget && window.widget.body) {
          window.widget.body.appendChild(rootElement);
        } else {
          document.body.appendChild(rootElement);
        }
      }

      // Create or reuse the root
      if (!root) {
        root = ReactDOM.createRoot(rootElement);
      }

      root.render(
        <StyledEngineProvider injectFirst>
          <MemoryRouter initialEntries={["/"]}>
            <CssBaseline />
            <App />
          </MemoryRouter>
        </StyledEngineProvider>
      );
    });
  });
}

export default function () {
  if (window.widget) {
    let hasOnLoadRun = false;

    window.widget.addEvent("onLoad", () => {
      if (hasOnLoadRun) return;
      hasOnLoadRun = true;
      start();
    });
  } else {
    console.error(
      "[index.js] ❌ Widget not detected! onLoad cannot be registered."
    );
  }
}
